<script>

    import AppBarPage from '@/components/AppBar.vue'  // 异常处理 异常关闭
    import {httpHandle} from '@/http/http'  // api
    import {RepairmanHTTP} from '@/http/equipment/repairs'   // api
    import UploaderImageComponents from '@/packages/UploaderImage.vue'

    import SelectComponents from '@/packages/Select.vue'

    import { showSuccessToast,showFailToast } from 'vant'

    import moment from "moment"

  export default {
    components:{
        AppBarPage,
        UploaderImageComponents,
        SelectComponents
    },
    data: () => ({
        bufferRow:{},  // 行数据

        bufferFileListPreviewInitiatePath:[],   // 图片预览 1
        bufferFileListPreviewManagePath:[],   // 图片预览 2


        

        influence:"",   // 影响台套
        time:"",         // 影响时长
        appraise:"",    // 评价


    }),
    created(){
        this.initFunc()
        

        
    },
    methods: {
        // 数据字典 格式化
        FormatDictionary(key="",valueKey=""){
            const _bufferDictionaries=JSON.parse(localStorage.getItem("bufferDictionaries")||"{}")
            const _obj=(_bufferDictionaries[key] || [] )
            const _option=_obj.filter(o=>valueKey==o.value)[0]||{}
            return _option
        },
        // 初始化
        async initFunc(){
            const {ttQmAbnormalId}=this.$route.query

            // 

            const {code,data={}}= await httpHandle({
                url:'/iiot/abnormal',
                method:"get",
                url_RESTful:`/${ttQmAbnormalId}`   
            })

            if(code==200){
                this.bufferRow=data

                this.time=data.closeDuration||"" // 影响时长


                // 图片预览 11
                if(data.initiatePath){
                    this.bufferFileListPreviewInitiatePath=data.initiatePath.split(',').map(o=>Object.assign({url:o}))
                }

                // 图片预览 22
                if(data.managePath){
                    this.bufferFileListPreviewManagePath=data.managePath.split(',').map(o=>Object.assign({url:o}))
                }
            }


        },
        // 关闭
        async forwardHandle(){
            const {pageAction}=this.$route.query

            const {bufferRow}=this

  
            if(this.time==""){
                showFailToast("影响时长必填！")
                return
            }


            const _json={
                ...bufferRow,  // 行数据
                closeSets: this.influence, /**  影响台套 */
                closeDuration: this.time,/**  影响时长 */
                closeAppraise: this.appraise,   /** 评价*/

            }

            const {code,data={}}= await httpHandle({
                url:'/iiot/abnormal/close',
                method: "post",
                payload:_json
            })

            if(code==200){
                showSuccessToast("提交成功！")

                if( pageAction==2 ){
                    this.$router.push({
                        path:'/anomalyInitiate/index/closeIndex', 
                        query:{ }
                    }) 
                }else{
                    this.$router.push({
                        path:'/anomalyInitiate/index', 
                        query:{ }
                    }) 
                }


            }
        }
    },
  }
</script>